import {getAnswer} from "@/components/azureQnA";
import {store} from "@/store"

const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
  if (!SpeechRecognition) {
    // uh shit browser doesn't support speech, do things here
  }

  const recognition = new SpeechRecognition()


recognition.continuous = true;
recognition.interimResults = true;
recognition.lang = 'en-US';

let transcript = ''
//Getters
let isListeningState = function () {
    return store.getters.isListeningState;
}

let isOnState = function () {
    return store.getters.isOnState;
}

let isSpeakingState = function () {
    return store.getters.isSpeakingState;
}

let userSpeechInputUpdated =  function () {
    return store.getters.userSpeechInputUpdated;
}

let startRecognition = function () {
    try {
        recognition.stop()
        recognition.start()

    } catch (e) {
        console.log(e.message);
    }
};


let stopRecognition = function () {
    try {
        recognition.stop();
        console.log('Recognition Stopped')
    } catch (e) {
        console.log(e.message)
    }

}


recognition.onstart = function (){
    console.log('Recognition Started')
    console.log('Value of Speaking inside onstart event',isSpeakingState())
}

recognition.onresult = function(event) {
    let spResult = event.results[event.resultIndex];


    if (isOnState() && isListeningState()) {
        if(!isSpeakingState()){
            transcript = event.results[event.results.length - 1][0].transcript;
            store.commit('updateUserSpeechInput', transcript)
        }
        else{
            transcript = ''
        }
    } else if (transcript.length >= 1 && isListeningState()) {
        if (isSpeakingState()) {
            transcript = event.results[event.results.length - 1][0].transcript;
            store.commit('updateUserSpeechInput', transcript)
        }else{
            transcript = ''
        }
    } else {
        transcript = ''
    }


    if (spResult.isFinal && transcript !== '') {
        if (isListeningState() && transcript.split(" ").length <= 50) {
            console.log('The transcript is  ', transcript)
            getAnswer(userSpeechInputUpdated())
        }
    }
}

recognition.onend = function() {
    console.log('Recognition Stopped')
    console.log('Value of Speaking inside onend event',isSpeakingState())
    store.commit('updateListeningFlag', false)
    if(!isSpeakingState()){
         startRecognition()
    }


}

export {startRecognition}
export {stopRecognition}


