<template>
  <v-responsive :aspect-ratio="16/9">

    <!--  ---------------------------------------------------------------ROW1 -----------------------------------------------  -->
    <!--    Back to Home ICON and Header -->
    <v-row>
      <v-col cols="1" class="mt-3">
        <v-icon size="60" @click="$router.push('/');onBack()" class="pl-4">mdi-arrow-left-circle</v-icon>
      </v-col>
      <v-col cols="11" align="center" class="mt-4">
        <p class="text-h4 pr-15">Maps Directory</p>
        <v-divider align="center" justify="center" width="600" height="20"></v-divider>
      </v-col>
    </v-row>

    <!--   ----------------------------------------------------------ROW 2 ----------------------------------------------  -->

    <v-row class="mt-3">
      <v-col cols="2" class="pr-0">
        <v-card color="primary" class="rounded-0 " height="70px" elevation="0">
          <h2 class="pa-4 pl-10 text--white"> Map's Directory </h2>
        </v-card>
      </v-col>
      <v-col cols="10" class="pl-0">
        <v-card color="cyan" height="70px" elevation="0" class="rounded-0">
          <v-tabs v-model="active_tab" height="70px" centered background-color="primary" >
<!--            <v-tabs-slider color="black" style="height: 4px"></v-tabs-slider>-->
            <v-tab v-for="(floor,i) in floors" :key="i" class="pr-10 text-h5 white--text" @click="selectImage(floor.mapName,floor.floorNum)">
              {{ floor.floor }}
            </v-tab>
          </v-tabs>
        </v-card>
      </v-col>
    </v-row>

    <!--    -------------------------------------------------------ROW3 ------------------------------------------------- -->

    <v-row>
      <v-col cols="2">
        <v-btn class="ma-2" outlined color="primary" width="275" height="60" v-for="(map,i) in maps" :key="i"
               @click="selectImage(map.mapPath, map.mapFloor)">{{ map.mapName }}
        </v-btn>
      </v-col>

      <v-col cols="10">
        <v-row>
          <v-img :src="imageSrc()" max-height="600"></v-img>
        </v-row>
        <v-row justify="center">
            <div align="left">
              <avatar :initial-width="275" text-bubble="left"></avatar>
            </div>
        </v-row>
      </v-col>
    </v-row>
  </v-responsive>
</template>

<script>

//import storageRef from "@/firebase.js"
import avatar from "@/components/avatar";
import db from "@/firebase"

export default {
  name: "Maps",
  components: {
    avatar
  },
  data() {
    return {
      active_tab: 0,
      floors: [
        {floor: 'First Floor', mapName: 'd1_001_child_support_hearing_room.gif', floorNum: 1},
        {floor: 'Second Floor', mapName: 'd1_003_div_1_judge_f_mathew.png', floorNum: 2},
        {floor: 'Third Floor', mapName: 'o-sheriff.png', floorNum: 3}
      ],
      maps: [],
      imagePath: 'd1_001_child_support_hearing_room.gif',
    }
  },
  created() {
    if(this.onlineState){
      this.state = 'server'
    }else{
      this.state = 'cache'
    }
    console.log('What is the state',this.state)

    db.collection('maps').get({ source: this.state }).then(querySnapshot => {
          querySnapshot.forEach(doc => {

            const data = {
              'id': doc.id,
              'mapFloor': doc.data().mapFloor,
              'mapPath': doc.data().mapPath,
              'mapName': doc.data().mapName
            }
            this.maps.push(data)
          })
        }
    )
  },
  methods: {
    imageSrc () {
      return require('@/assets/' + this.imagePath);
    },

    selectImage(path, floor) {
      this.imagePath = path,
      this.active_tab = floor - 1
    },
    onBack() {
      avatar.methods.stopSpeak()
      this.$store.commit('updateUserSpeechInput','I am Listening. You can ask me a Question')
    }

  },
  computed:{
    userSpeechInputUpdated() {
      return this.$store.getters.userSpeechInputUpdated;
    },
    onlineState(){
      return this.$store.getters.onlineState;
    },
  },
  mounted() {
    avatar.methods.speak(this.userSpeechInputUpdated)
}
  ,
}
</script>

<style scoped>

body {
  font-family: Arial;
}

/deep/ .v-card {
  border: 0px;
  border-right-color: cyan;
}

/deep/ .v-toolbar {
  border: 0px;
  border-left-color: cyan;
}

.v-row {
  height: 100px;
}

#card-wrapper {
  background-color: rgba(30, 101, 39, 0.88);
  border-radius: 35px;
  color: white;
  text-align: center;
  border-style: solid;
  border-color: white;
  border-width: 2px;
  padding: 0 1em;
  bottom: 30px;
  width: 800px;
  position: absolute;
  }

.v-card p {
  font-size: 3em;
}

.v-card h2{
  color: white;
}

/deep/ #text-bubble {
  width: 800px;
  z-index: 0;
}

/deep/ .left-bubble {
  top: 70%;
  left: -10%;
  z-index: 0;
}

#avatar-wrapper {
  position: absolute;
  right: 10px;
  bottom: 10px;
  background-color: white;
//box-shadow: 0 0 16px 3px rgba(107,21,48,0.82);
  border-radius: 5px;
  z-index: 0;
}

</style>
