<template>
  <v-dialog v-model="dialog" persistent max-width="1200">
    <v-card align="center">
        <v-img :src="imageSrc()" max-height="800" contain></v-img>
        <v-btn @click="closeDialog()" width="100" color="red" class="mt-10 mb-5">Close</v-btn>
    </v-card>
  </v-dialog>
</template>

<script>
import {EventBus} from "@/event-bus";
import avatar from "@/components/avatar";
export default {
  name: "imageDialog",
  data(){
    return{
      dialog: false,
      imagePath: '',
    }
  },
  computed: {
    isOnState() {
      return this.$store.getters.isOnState;
    }
  },
  methods: {
    imageSrc() {
      if (this.dialog){
        console.log('Image Path','@/assets/' + this.imagePath)
        return require('@/assets/' + this.imagePath);
      }
      else
        return this.imagePath;
    },
    closeDialog(){
      this.dialog = false
      avatar.methods.stopSpeak();
      this.$store.commit('updateUserSpeechInput','I am Listening. You can ask me a Question')
    }
  },
  mounted() {
    EventBus.$on('openImageDialog', image => {
        this.imagePath = image
        this.dialog= !this.dialog
    }),
        EventBus.$on('closeImageDialog',() => {
          this.dialog = false
          console.log('Value of On State', this.isOnState)
          if(this.isOnState){
            this.$store.commit('updateUserSpeechInput', 'I am Listening. You can ask me a Question')
          }

        })
  }
}
</script>

<style scoped>
.v-dialog{
  top: 8cm;
}
</style>
