<template>

  <v-dialog v-model="dialog" width="700" height="500" transition="dialog-top-transition">
    <v-card>
      <v-card-title class="black--text text-center justify-center">
        <p>Please Select an Option</p>
      </v-card-title>
      <v-card-text align="center">
        <v-btn v-for="(optionList,i) in optionLists" :key="i" color="grey" width="600" height="70" class="mt-5" @click="currentSelection(optionList)">{{ optionList }} </v-btn>
      </v-card-text>
    </v-card>
  </v-dialog>

</template>

<script>
import {EventBus} from "@/event-bus";

export default {
  name: "optionsDialog",
  data() {
    return {
      dialog: false,
      optionLists: ["Missed Court Date", "Can't make scheduled Court Date", "Driving Privileges", "Forfeited License"]
    }
  },
mounted() {
  EventBus.$on('openOptionsDialog', () => {
    this.dialog =!this.dialog
  })
},
  methods: {
    currentSelection(result){
      console.log('What is Selected', result)
      this.dialog = false
    }
  }
}
</script>

<style scoped>
/deep/ .v-dialog{
  position: absolute;
  top: 4em;
}

#v-btn{
  font-size: 1.5em;
}
</style>
