let isSpeakingState = function () {
    return store.getters.isSpeakingState;
}

let emitGlobalImageCLoseEvent = () => {
    EventBus.$emit('closeImageDialog');
}

window.vh_talkStarted = function () {
    store.commit('updateIsSpeaking', true)
    console.log('Value of Speaking inside vh_talkStarted',isSpeakingState())
    stopRecognition()
    console.log('I am called inside vh_talkStarted')
},

window.vh_talkEnded = function (){
        console.log('Talk Ended')
        store.commit('updateIsSpeaking', false)
        console.log('Value of Speaking inside vh_talkEnded',isSpeakingState())
        startRecognition()
        setTimeout(function() {emitGlobalImageCLoseEvent()},500)
        //store.commit('updateUserSpeechInput', 'I am Listening. You can ask me a Question')
        console.log('I am called inside vh_talkEnded')
    }


window.newApp = new Vue({
    vuetify,
    router,
    store: store,
    render: h => h(App, { props: {}}),
    data: {
        avatar: undefined,
        curLanguage: 'en',
    },
  watch: {
    $route: function (to, from) {


      if (to.path === '/') {
        console.log('Yo')
      }
      else if (from.path === '/') {
        console.log('Yo')
      }

      const that = this;
      this.$nextTick(() => {
        const newAvatarComp = that.findAvatarComponent(that);
        console.log('New Avatar Component width',newAvatarComp.width)
        const newWidth  = newAvatarComp.width - 1;
        that.$nextTick(() => {
          newAvatarComp.$el.replaceChild(that.avatar.$el.children[0], newAvatarComp.$el.children[0]);
          that.avatar = newAvatarComp;
          that.avatar.width = newWidth;
        })
      });
    },
  },
  methods:{
    findAvatarComponent(parent) {
      let found = parent.$children.find(child => { return child.$options.name === "avatar"; });
      console.log('Found', found)
      for (let i = 0; !found && i < parent.$children.length; i++) {
        found = this.findAvatarComponent(parent.$children[i]);
      }
      return found;
    },
  },
  mounted(){
    this.avatar = this.findAvatarComponent(this);
    startRecognition();


  }
}).$mount('#app')

