<template>
  <v-responsive :aspect-ratio="16/9" class="flex">

    <!--    ----------------------------------------------------- ROW 1 ------------------------------------------------  -->

    <v-row>
      <v-col cols="1" class="mt-2">
        <v-icon size="60" @click="$router.push('/');onBack()" class="pl-4">mdi-arrow-left-circle</v-icon>
      </v-col>
      <v-col cols="11" align="center" class="mt-4">
        <p class="text-h4">Frequently Asked Questions</p>
        <v-divider align="center" justify="center" width="600" height="30"></v-divider>
      </v-col>
    </v-row>

    <!--    ---------------------------------------------------- ROW 2 --------------------------------------------------  -->

    <v-row class="mt-3">
      <v-col cols="12" align="center">
        <v-text-field v-model="search" label="Type a Keyword or a Phrase to search"
                      outlined prepend-inner-icon="mdi-magnify">
        </v-text-field>
      </v-col>
    </v-row>

    <!--    ---------------------------------------------------- ROW 3 ---------------------------------------------------- -->

    <v-row class="pb-2">
      <v-col cols="12" align="center">
        <v-card align="center" width="1800" height="550" class="pa-3">
          <v-expansion-panels multiple>
            <v-expansion-panel v-for="(faq,i) in filtered_search" :key="i" class="mt-2">
              <v-expansion-panel-header class="white--text text-h5 grey" >{{ faq.question }}</v-expansion-panel-header>
              <v-expansion-panel-content class="black--text text-h5 pa-3" align="left">{{ faq.answer }}</v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-card>
      </v-col>
    </v-row>

    <!--    ---------------------------------------------------- ROW 4 ---------------------------------------------------- -->
    <v-row justify="center">
      <div>
        <avatar :initial-width="275" text-bubble="right"></avatar>
      </div>
    </v-row>

  </v-responsive>
</template>

<script>

import db from '@/firebase'
import avatar from "@/components/avatar";

export default {
  name: "Faqs",
  components: {
    avatar
  },
  data() {
    return {
      search: '',
      faqs: [],
      state: ''
    }
  },
  computed:{
    userSpeechInputUpdated() {
      return this.$store.getters.userSpeechInputUpdated;
    },
    onlineState(){
      return this.$store.getters.onlineState;
    },
    filtered_search: function () {
      return this.faqs.filter((faq) => {
        return faq.question.toLowerCase().match(this.search.toLowerCase())
      })
    }
  },
  created() {

    if(this.onlineState){
      this.state = 'server'
    }else{
      this.state = 'cache'
    }
    console.log('What is the state',this.state)


     db.collection('faqs').get({ source: this.state })
        .then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const sample = doc.data().answer

        const data = {
          'id': doc.id,
          'question': doc.data().question,
          'answer': sample.replaceAll("\\n", "\n")
        }
        console.log('Call to Database')
        this.faqs.push(data)
      })
          let source = querySnapshot.metadata.fromCache ? "local cache" : "server";
          console.log("Data came from " + source);
    })


    db.collection("update").doc('online').update({
      goOnline : false
    })
  },
  methods:{
    onBack() {
      avatar.methods.stopSpeak()
      this.$store.commit('updateUserSpeechInput','I am Listening. You can ask me a Question')
    }
  },
  mounted(){
    avatar.methods.speak(this.userSpeechInputUpdated)
  }
}
</script>

<style scoped>
/deep/ .v-text-field {
  width: 1800px;
}

/deep/ .v-card {
  overflow-y: auto;
  border-style: double;
}

/deep/ .v-expansion-panels {
  width: 95%;
}

/deep/ .avatar {
  z-index: 0;
}

#card-wrapper {
  background-color: rgba(30, 101, 39, 0.88);
  border-radius: 35px;
  color: white;
  text-align: center;
  border-style: solid;
  border-color: white;
  border-width: 2px;
  padding: 0 1em;
}

.v-card p {
  font-size: 3em;
}

#avatar-wrapper {
  position: absolute;
  left: 10px;
  bottom: 10px;
  background-color: white;
//box-shadow: 0 0 16px 3px rgba(107,21,48,0.82);
  border-radius: 5px;
  z-index: 0;
}

/deep/ #text-bubble {
  width: 1550px;
  z-index: 0;
}

/deep/ .right-bubble {
  top:70%;
  z-index: 0;
}

/*/deep/ .avatar {*/
/*  bottom: 5cm;*/
/*}*/
</style>
