    initiateCamera() {
      const that = this
      that.video = document.getElementById('video');

      navigator.mediaDevices.getUserMedia({
        'audio': false,
        'video': {width: 500, height: 500, frameRate: {max: 20}}
      })
          .then(
              stream => {
                that.video.srcObject = stream;
                that.video.play().then(
                    () => {
                      this.video.width = this.video.videoWidth;
                      this.video.height = this.video.videoHeight;
                      tf.setBackend('webgl').then(
                          () => {
                            console.log('Webgl is the backend')
                            blazeFace.load().then(
                                model => {
                                  console.log('Blaze Face loaded')
                                  that.$store.commit('updateUserSpeechInput', 'Hi! I am Erica. You can "Ask me a question"')
                                  setInterval(function () {
                                        that.prediction(model)
                                      }
                                      , 1000);
                                }
                            )
                          }
                      )
                    }
                )
              }
          )
    },

    prediction(model) {
      const that = this
      const returnTensors = false;
      const flipHorizontal = true;
      const annotateBoxes = true;
      this.faceDistance = 0;

      model.estimateFaces(this.video, returnTensors, flipHorizontal, annotateBoxes)
          .then(
              prediction => {
                //console.log('Length of Prediction', prediction.length)
                if (prediction.length > 0) {
                  for (let i = 0; i < prediction.length; i++) {
                    const start = prediction[0].topLeft;
                    const end = prediction[0].bottomRight;
                    that.faceDistance = (((start[0] - end[0]) / 500) * 100).toFixed(2);

                    if (that.faceDistance >= 18 && prediction[0].probability[0] >= 0.95) {


                      let rEyeEarDiff = prediction[0].landmarks[0][0] - prediction[0].landmarks[4][0];
                      let lEyeEarDiff = prediction[0].landmarks[1][0] - prediction[0].landmarks[5][0];

                      if (rEyeEarDiff < -15 && lEyeEarDiff > 15) {
                        //console.log('Probability of the Prediction', prediction[0].probability[0])
                        //console.log('Face Detection condition passed')
                        this.$store.commit('updateOnFlag', true)
                        that.count = 0
                        if (that.isOnState) {
                          that.timer = 10000
                          clearTimeout(that.timeout1)
                          //console.log('Timeout value', that.timeout1)
                          if (!that.isSpeakingState) {
                            if (that.greet === 0) {
                              that.greet = 1
                              that.$store.commit('updateIsSpeaking', true)
                              that.$store.commit('updateUserSpeechInput', 'Hi! I am Erica. How can I help you?')
                              avatar.methods.speak(that.userSpeechInputUpdated)
                            }
                            if (!that.isSpeakingState) {
                              that.currentState = 'I am Listening'
                              that.finalTranscript = ''
                            }

                            this.$store.commit('updateListeningFlag', true)
                          }
                        } else {
                          //that.timer = 0
                          that.timer = 10000
                        }

                      } else {
                        //console.log('Person not facing towards Camera')
                        that.$store.commit('updateOnFlag', false)
                        //that.$store.commit('updateListeningFlag', false)
                      }
                    } else {
                      //console.log('Person not in scope of Device')
                      that.$store.commit('updateOnFlag', false)
                      //that.$store.commit('updateListeningFlag', false)

                      // below is written to execute greeting method only once
                      that.count = that.count + 1
                      if (that.count === 1) {
                        that.greeting()
                      }
                    }
                  }
                } else {
                 // console.log('Person not in front of Camera')
                  that.$store.commit('updateOnFlag', false)
                  //that.$store.commit('updateListeningFlag', false)

                  // below is written to execute greeting method only once
                  that.count = that.count + 1
                  if (that.count === 1) {
                    that.greeting()
                  }
                }
              })
    },
    greeting() {
      const that = this

      if (!that.isOnState) {

        that.timeout1 = window.setTimeout(function () {
          if (that.greet === 1) {
            if (window.location.pathname !== '/') {
              that.$router.push({path: '/'})
            }
            console.log('Person Left')
            that.emitGlobalImageCLoseEvent()
            that.emitGlobalTextCLoseEvent()
            that.count = 0
            that.greet = 0
            that.timer = 0
            that.$store.commit('updateUserSpeechInput', 'Hi! I am Erica. You can "Ask me a question"')
            that.$store.commit('updateListeningFlag', false)
            that.$store.commit('updateOnFlag', false)
            that.$store.commit('updateIsSpeaking', false)


          }
        }, that.timer)

      }
    },
  },
  mounted() {
    this.initiateCamera();

  }
